import React, { Component } from 'react';
import TranslatableLabel from '../translatable_label/translatable_label';
import Carousel from 'react-bootstrap/Carousel';
import Modal from 'react-bootstrap/Modal';
import Button from 'react-bootstrap/Button';
import code_thumb from './demo_images/code_alive/img.png';

class CodeAlive extends Component {
    state = {
        show: false,
    }

    handleShow() {
        // alert();
        this.setState({
            show: true
        })
    }
    handleClose() {
        this.setState({
            show: false
        })
    }

    render() {
        return (
            <div className="height-100">

                <div className="skill-title">
                    <div className="skill-title-text">
                        Code Alive
                    </div>
                </div>
                <div className="demo-container">
                    <img className="exp-thumbnail" src={code_thumb} ></img>
                    <div className="demo-btn-container vertical-center-content">
                        <Button onClick={this.handleShow.bind(this)}>
                            <a className="demo-link" href="https://adriandiazcode.github.io/code_alive_demo/">
                                <TranslatableLabel
                                    type_speed={40}
                                    start_delay={0}
                                    is_typed={true}
                                    spa={`Ver demo`}
                                    eng={`See demo`}
                                    ger={`Demo sehen`}
                                />
                            </a>
                        </Button>
                    </div>
                </div>
            </div>
        );
    }
}

export default CodeAlive;