import React, { Component } from 'react';
import TranslatableLabel from '../translatable_label/translatable_label';
import { connect } from 'react-redux';
import './demos.css';
import Carousel from 'react-bootstrap/Carousel';
import VChess from './vchess';
import Description from './description';
import CodeAlive from './code_alive';
import ReactVisibilitySensor from 'react-visibility-sensor';
// import IOT from './IOT';
// import Mintransporte from './mintransporte';


class Skills extends Component {
    state = {
        is_visible: false,
        can_slide: false,
    }

    on_visibility(is_visible) {

        const can_slide = false;
        this.setState({ is_visible, can_slide });
        if(is_visible){
            setTimeout(() => {
                this.setState({can_slide:true});
            }, 3000);
        }

    }



    componentDidMount() {
        // this.refs.arrow_left.addEventListener('click', () => {
        //     this.refs.container.scrollBy(-500, 0);
        // });
        // this.refs.arrow_right.addEventListener('click', () => {
        //     this.refs.container.scrollBy(500, 0);
        // });
    }
    render() {
        return (
            <div className="skills-section">
                {/* <h3 className="skills-section-title">
                    <TranslatableLabel is_typed={true} eng="Some of my projects" spa="Algunos proyectos" ger="Einige meiner Projekte"></TranslatableLabel>
                </h3> */}
                <div ref="container" className="skills-section-container">
                    <div
                        className="height-100"
                    >
                        <ReactVisibilitySensor
                            onChange={this.on_visibility.bind(this)}
                        >
                            <Carousel
                                className={"height-100"}
                                interval={3000}
                                // interval={this.state.is_visible ? 5000 : 0}
                                // activeIndex={0}
                                defaultActiveIndex={1}
                                activeIndex={this.state.can_slide ? undefined : 0}
                                // slide={!this.state.is_visible}
                            >
                                <Carousel.Item className="skill-category height-100">
                                    <Description />
                                </Carousel.Item>

                                <Carousel.Item className="skill-category height-100">
                                    <CodeAlive />
                                </Carousel.Item>

                                <Carousel.Item className="skill-category height-100">
                                    <VChess />
                                </Carousel.Item>

                            </Carousel>
                        </ReactVisibilitySensor>
                    </div>
                </div>
            </div>
        );
    }
}

export default connect(null, null)(Skills);